.slider {
    height: 100vh; }

.slider-item {
    height: 100vh !important;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center; }


.slider-text {
    color: #000;
    text-align: center;
    font-family: Annabelle;
    font-size: 30px;
    font-weight: normal;
    letter-spacing: 4.5px;
    position: absolute;
    top: 37%;
    left: 70px;
    transform: translateY(-70%); }

.slick-arrow {
    position: absolute;
    width: 100px;
    height: 100vh;
    display: block;
    transform: translateY(-50%);
    border: none;
    background-repeat: no-repeat;
    background-position: center center;
    background-color: transparent;
    opacity: .5;
    top: 50%;
    cursor: pointer;
    transition: .4s;
    z-index: 1;
    outline: 0; }

.slick-arrow:hover {
    transition: .4s;
    opacity: 1; }

.slick-prev {
    background-image: url('../img/back-arrow.png');
    left: 0; }

.slick-next {
    background-image: url('../img/front-arrow.png');
    right: 0; }
