.review-wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px; }

.review {
    box-sizing: border-box;
    width: 460px;
    min-height: 260px;
    background-color: #ebebeb;
    display: flex;
    justify-content: center;
    font-family: Montserrat, sans-serif;
    font-weight: bold;
    padding: 15px; }

.review-avatar {
    width: 125px;
    height: 125px;
    margin-right: 20px;
    border-radius: 50%; }

.review-header {
    color: #000;
    font-size: 16px;
    line-height: 51px;
    letter-spacing: 1.95px; }

.review-text {
    color: #636363;
    font-size: 12px;
    line-height: 20px;
    padding-right: 15px;
    letter-spacing: 1.5px; }
