.feed-back {
    height: 500px;
    background: url("../img/feed-back-background.jpg") no-repeat center center;
    background-size: cover;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    h2, form {
        transform: translateY(-50%); } }


.feed-back-header {
    color: #fefefe;
    font-family: Neucha;
    font-size: 45px;
