* {
    margin: 0;
    padding: 0; }
body {
  min-width: 1110px; }
li {
    list-style: none; }

.container {
  width: 940px;
  margin: 0 auto; }

.section-header {
  color: #000;
  font-family: Annabelle;
  font-size: 30px;
  font-weight: bold;
  line-height: 51px;
  letter-spacing: 4.5px;
  margin-bottom: 65px;
  margin-top: 150px;
  text-align: center; }

input[type="text"] {
  box-sizing: border-box;
  appearance: none;
  outline: 0;
  border: none;
  display: inline-block;
  width: 400px;
  padding: 15px 17px;
  border-radius: 15px;
  background-color: #fff;
  color: #272727;
  font-family: Neucha;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1.8px;
  transition: .4s;
  margin-right: 20px; }

input[type="text"]:hover, input[type="text"]:focus {
  transition: .4s;
  background-color: #FFDF60; }

input[type="submit"] {
  appearance: none;
  outline: 0;
  padding: 20px 70px;
  border-radius: 30px;
  border: 1px solid #fff;
  background-color: #111;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1.08px;
  cursor: pointer;
  transition: .4s; }

input[type="submit"]:hover {
  transition: .4s;
  background-color: #313331; }

@font-face {
  font-family: "Annabelle";
  src: url("../fonts/Annabelle.ttf"); }

